import React from "react"
import { Note as NoteType, Chord as ChordType, getChordNotes, ChordSequence as ChordSequenceType, fromStringToChord, Bar, getNextSemitone, getPrevSemitone, fromStringToNote, toStringFromNote } from "../components/utils"
import { useState } from "react"
import Scale from "../components/scale"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Chord from "../components/chord"
import { set } from "lodash"

const iRealReader = require('ireal-reader');

export default function ChordPage(): JSX.Element {
  const [topChord, setTopChord] = useState<ChordType>({ tonic: { base: "C", modifier: 0 }, type: "MAJOR", addOns: [] });

  return (<Layout currentPage="Music" currentSubPage="Chords">
    <SEO title="Chords" description={"Given chord find all the notes you can play in jazz improv."} keywords={['music', 'theory', 'chords', 'jazz', 'generate', 'notes', 'improv', 'jazz improvisation chord generation']} />
    <div >
      <h2>Main Chord</h2>
      <p><Chord chord={topChord} setChord={setTopChord} /></p>
    </div>
    <div>
      <h2>Available Notes:</h2>
      <p>    <div className="lcars-card">
        <div className="card-body"><Scale notes={getChordNotes(topChord)} /></div></div></p>
    </div>
    <div>
      <h2>Full Song</h2>
      <p><ChordSequence /></p>
    </div>
  </Layout>
  );
}



function ChordSequence(): JSX.Element {
  const [url, setUrl] = useState<string>("");
  const [song, setSong] = useState<ChordSequenceType | undefined>(undefined);
  const [title, setTitle] = useState<string>("");
  const [key, setKey] = useState<NoteType | undefined>(undefined);

  const getSong = () => {
    console.log("click")
    const parseResult = iRealReader(url)
    const songResult = parseResult.songs[0]
    const bars = songResult.music.measures as Array<Array<string>>;
    setKey(fromStringToNote(songResult.key))
    setSong(
      {
        bars: bars.map(
          bar => {
            return {
              chords: bar.map(
                chordString => fromStringToChord(chordString) ?? null)
            }
          }
        )
      }
    )
    setTitle(songResult.title)
  }

  return <div>
    <div className="d-flex flex-row">
      <div className="align-self-center mr-5">iReal URL:{"  "}</div>

      <input
        type="text"
        className="lcars-form-control lcars-big-input"
        placeholder={url ?? ""}
        aria-label="Note"
        aria-describedby="basic-addon1"
        onChange={(e) => setUrl(e.target.value)}
      />
      <button disabled={url === null} onClick={getSong}>Process</button>

    </div>
    {key && <div className="buttons d-flex flex-row">
      <h2 className="align-self-center mr-5">{title ?? "Unknown"}{" in the key of "}{toStringFromNote(key)}</h2>
      <button
        className="orange"
        disabled={!song || !key}
        onClick={() => {
          setSong({
            bars: song!.bars.map(
              bar => {
                return {
                  chords: bar!.chords.map(c => { return c ? transposeChordDown(c) : c; })
                }
              }
            )
          })
          setKey(getPrevSemitone(key!, true))
        }}
      >
        Transpose down
      </button>
      <button
        className="orange"
        disabled={!song || !key}
        onClick={() => {
          setSong({
            bars: song!.bars.map(
              bar => {
                return {
                  chords: bar!.chords.map(c => { return c ? transposeChordUp(c) : c; })
                }
              }
            )
          })
          setKey(getNextSemitone(key!, true))
        }}
      >
        Transpose up
      </button>


    </div>}

    {song && <><h2>Use the following Notes in the song:</h2> <Song data={song} /></>}
  </div>
}

function jiggleMyBars(arr: Array<Bar>): Array<Array<Bar>> {
  const res = [];
  for (let i = 0; i < arr.length; i += 4) {
    const chunk = arr.slice(i, i + 4);
    res.push(chunk);
  }
  return res;
}


function Song({ data }: { data: ChordSequenceType }): JSX.Element {
  if (data === null) {
    return <div />;
  }

  const jiggled = jiggleMyBars(data.bars)
  console.log(JSON.stringify(data))
  console.log("AND JIGGLE JIGGLE")
  console.log(JSON.stringify(jiggled))
  return <div className="flex-wrap d-flex flex-row">
    {data.bars.map(a => {
      return <div className="d-flex flex-row flex-fill">
        {a.chords.map(c => { return <div>{c === undefined ? <div /> : <Scale notes={getChordNotes(c)} />}</div> })}
        <div>{"|"}</div></div>
    })}
  </div>
}

function transposeChordDown(chord: ChordType): ChordType {
  return { tonic: getPrevSemitone(chord.tonic, true), type: chord.type, addOns: chord.addOns }
}

function transposeChordUp(chord: ChordType): ChordType {
  return { tonic: getNextSemitone(chord.tonic, false), type: chord.type, addOns: chord.addOns }
}

